	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Easter Fire 2001","date":"14 Apr 2001","slug":"/2001-04-14-easter-fire-2001/","tags":"Y2001"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/04/achim.jpg\" height=\"270\" width=\"325\" border=\"0\" alt=\"achim.jpg: \" /></p>\n<p>Our neighbor Achim Peters and MamaMaus at the Lüllau Easter Fire. We promised Achim we'd put his picture on our home page. More pictures <a href=\"http://albums.photopoint.com/j/AlbumIndex?u=185392&a=12581189&f=0&sp=1\">here</a></p>"},"next":{"fields":{"title":"I'm on overload in my head","date":"13 Apr 2001","slug":"/2001-04-13-im-on-overload-in-my-head/"}},"previous":{"fields":{"title":"White Easter","date":"15 Apr 2001","slug":"/2001-04-15-white-easter/"}}},{"node":{"fields":{"title":"I'm on overload in my head","date":"13 Apr 2001","slug":"/2001-04-13-im-on-overload-in-my-head/","tags":"Y2001"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/04/splashday.jpg\" height=\"244\" width=\"325\" border=\"0\" alt=\"splashday: \" /></p>\n<p><i>Since today is a holiday we moved up Christopher's  weekly bathday. He didn't mind and splashed at his best.</i></p>\n<p><a href=\"http://www.dangerousmeta.com/\">Garret</a>, my windows telnet client of choice is <a href=\"http://hp.vector.co.jp/authors/VA002416/teraterm.html\">TeraTerm Pro</a>. It's clean, free, and has an <a href=\"http://www.zip.com.au/~roca/ttssh.html\">SSH extension</a>.</p>\n<p>6:45 am on a holiday, and Christopher is already awake. I thought we wore him yesterday so we could sleep in.</p>\n<p>From the Dead.Com department, the Abendblatt had an article on Internet firms in Hamburg with big losses on the Neuer Markt. My employer made the top of the list, meaning the 92% decline in our stock since 03/2000 was the 2nd best of the 10 firms mentioned (i.e. the rest had bigger declines). And the past week our stock has actually doubled (from 2.20 to 4.90). My first set of options due next month are at 25.50, so we still have a ways to go.</p>"},"next":{"fields":{"title":"I just had to get out of bed","date":"09 Apr 2001","slug":"/2001-04-09-i-just-had-to-get-out-of-bed/"}},"previous":{"fields":{"title":"Easter Fire 2001","date":"14 Apr 2001","slug":"/2001-04-14-easter-fire-2001/"}}},{"node":{"fields":{"title":"I just had to get out of bed","date":"09 Apr 2001","slug":"/2001-04-09-i-just-had-to-get-out-of-bed/","tags":"Y2001"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/04/blossom.jpg\" height=\"320\" width=\"240\" border=\"0\" alt=\"Blossom: \" /></p>\n<p><i><b>Our first blossom in the garden. Spring is finally here. Thanks to Daylight Savings Time I can still see what's going on in the garden when I get home!</b></i></p>\n<p>Easter week. The spring German holidays coming up means a lot of 4-day work weeks; Good Friday, Easter Monday, May Day, Ascension, Pentecost. </p>\n<p>Mama just asked me where the past few page titles come from. Overload, from the Sugababes, and I'm noteven halfway through the song yet.</p>"},"next":{"fields":{"title":"Skipping school, gotta walk for air","date":"08 Apr 2001","slug":"/2001-04-08-skipping-school-gotta-walk-for-air/"}},"previous":{"fields":{"title":"I'm on overload in my head","date":"13 Apr 2001","slug":"/2001-04-13-im-on-overload-in-my-head/"}}}],"pathPrefix":"","first":false,"last":false,"index":1009,"pageCount":1101}}

